import React from 'react';
import {Link} from 'react-router';

export default function(props) {

  return (
    <sidebar>
      <section>
        <h4 className="menu-item" onClick={() => { props.toggleClick('boardTemplateToggle'); }}>
          <label>Templates</label>
        </h4>
        { props.boardTemplateToggle ? (
          <div>toggling</div>
          ) : null }
      </section>
      <hr />
    </sidebar>
  );
}



